import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { TaskService } from '../services/task.service';
import { WorkitemAction } from '../../entity/workitem-action';
import { CompleteContext, AssigneeInfo, CompleteResponse } from '../../entity';
import { NotifyService } from '@farris/ui-notify';
import { TaskSignaddComponent } from '../components/task-signadd/task-signadd.component';
import { DialogComponent } from '@farris/ui-dialog';
import { ActionEntity } from '../../entity/action-entity';
import { from, of, Observable } from 'rxjs';
import { I18nService } from '../services/i18n.service';
import { NextNode } from '../../entity/destination-nodes';
import { FlowNodeInfo } from '../../entity/excution-response';

declare const System: any;
@Component({
  selector: 'wf-workitem-handle',
  templateUrl: './wf-workitem-handle.component.html',
  styleUrls: ['./wf-workitem-handle.component.css']
})
export class WfWorkitemHandleComponent implements OnInit {
  private inWorkitemId: string;
  private taskActions: WorkitemAction[];
  completeContext = new CompleteContext();
  assigneeInfos: AssigneeInfo[];
  transferContext: {
    workItemId: string,
    comment: string
  };
  destActivities: { id: string, name: string }[];
  // å ç­¾å¾è¯¢ä¸ä¸æåæ°
  addSignContext: {
    workItemId: string,
    action: 'AddSignFront' | 'AddSignBehind'
  };
  needAddSign: boolean; // æ¯å¦éè¦å ç­¾
  selectAddSignees: string; // å ç­¾éä¸­äºº
  displayPlaceholder: string = "è¯·éæ©";//è·³è½¬çé»è®¤æ¾ç¤ºèç¹
  hasDefaultBranch = true;
  @ViewChild(TaskSignaddComponent)
  addSignCmp: TaskSignaddComponent;
  @ViewChild('commentModal')
  commentModal: DialogComponent;
  /**
   * ä»»å¡Id
   */
  @Input()
  set workitemId(workitemId: string) {
    this.inWorkitemId = (workitemId && workitemId.trim());
    if (this.inWorkitemId) {
      this.refreshActions();
    }
  }
  get workitemId() {
    return this.inWorkitemId;
  }

  /**
   * ä»»å¡åçå¨ä½åè¡¨
   */
  @Output()
  actions = new EventEmitter<WorkitemAction[]>();

  /**
   * ä»»å¡åçå®æäºä»¶
   */
  @Output()
  completed = new EventEmitter<boolean>();

  // å½åéä¸­å¨ä½åå¨ä½å®ç°
  preFuncList: any[] = [];
  webCptFunc: any;
  postFuncList: any[] = [];

  // å¼¹æ¡é«åº¦è®¾ç½®
  height = 420;

  actionsTitle = {
    pass: '',
    back: '',
    refuse: '',
    more: '',
    viewLog: '',
    viewFlowChart: '',
    viewBizlog: ''
  };

  constructor(
    private taskSvc: TaskService,
    private notify: NotifyService,
    private i18nService: I18nService,
  ) {
    const titles = this.i18nService.getValue('static.taskActions');
    Object.keys(this.actionsTitle).forEach(key => {
      this.actionsTitle[key] = titles[key];
    });
  }

  ngOnInit() {
  }

  /**
   * å·æ°å¨ä½åè¡¨
   */
  private refreshActions() {
    this.taskSvc.getWorkItemActions(this.inWorkitemId).subscribe((re: any) => {
      // ä¸´æ¶æ¹æ¡ï¼oDataä¸æ¯æèªå®ä¹function $expandå±å¼ï¼ä¸´æ¶åºååå¤ç
      this.taskActions = re.map(action => {
        action.parameters = JSON.parse(action.parameters);
        return action;
      });
      this.taskActions = this.taskActions.filter(a =>
        a.code !== 'ViewBizLog'
        && a.code !== 'ViewFlowChart'
        && a.code !== 'ViewExecuteForm');

      this.actions.emit(this.taskActions);
    }, error => {
      this.showError(error);
    });
  }

  /**
   * å¨ä½å®æåäºä»¶
   * @param result äºä»¶ç»æ
   */
  private completedEvt(result: boolean = true) {
    if (result) {
      this.refreshActions();
    }
    this.completed.emit(result);
  }

  /**
   * å®æå®¡æ¹ä»»å¡
   * @param actionCode ä»»å¡åçå¨ä½ç¼å·
   * @param comment? åçæè§
   */
  complete(actionCode: string, comment?: string) {
    switch (actionCode) {
      case 'AssignUser': { // ææ´¾
        this.assignee(this.inWorkitemId);
        break;
      }
      case 'Claim': // é¢ç¨
        this.taskSvc.claim(this.inWorkitemId).subscribe(r => {
          this.notify.success(this.i18nService.getValue('cueWords.successClaim'));
          this.refreshActions();
          this.completed.emit(false);
        }, error => {
          this.showError(error);
        });
        break;
      case 'Unclaim': // åæ¶é¢ç¨
        this.taskSvc.unClaim(this.inWorkitemId).subscribe(r => {
          this.notify.success(this.i18nService.getValue('cueWords.successUnClaim'));
          this.refreshActions();
          this.completed.emit(false);
        }, error => {
          this.showError(error);
        });
        break;
      case 'Retrieve': // åå
        this.taskSvc.retrieve({ workItemId: this.inWorkitemId, comment }).subscribe(r => {
          this.notify.success(this.i18nService.getValue('cueWords.successRetrieve'));
          this.completedEvt();
        },error => {
          this.showError(error);
        });
        break;
      case 'Transfer': { // ç§»äº¤
        this.transferContext = {
          workItemId: this.inWorkitemId,
          comment
        };
        break;
      }
      case 'AddSignFront': // å¾è¯¢
        this.addSignContext = {
          workItemId: this.inWorkitemId,
          action: 'AddSignFront'
        };
        break;
      default:
        this.executeTask(actionCode, comment);
    }
  }

  refreshList(sucess: boolean) {
    this.completedEvt(sucess);
  }
  // å¼¹åºå ç­¾å¸®å©
  runAddSign() {
    this.addSignContext = {
      workItemId: this.inWorkitemId,
      action: 'AddSignBehind'
    };
  }
  // å ç­¾å®æ
  addSignComplete(sucess: boolean) {

    if (this.addSignContext.action === 'AddSignFront') {
      this.refreshList(sucess);
    } else {
      this.completeContext.signAssignees = this.addSignCmp.selectedUsers.map(user => user.ID);
      this.selectAddSignees = this.addSignCmp.selectedUsers.map(user => user.Name).join(',');
    }
  }

  // å®¡æ¹ç¡®è®¤
  handleOk() {
    if(!this.completeContext.comment) {
      this.notify.info("è¯·è¾å¥å®¡æ¹æè§");
      return;
    }
    if(!this.hasDefaultBranch && this.completeContext.destinationActivityId.length <= 0) {//æ²¡æé»è®¤åæ¯å¹¶ä¸ç¨æ·æ²¡éæ©è·³è½¬èç¹ï¼ä¸åè®¸æäº¤
      this.notify.info("è¯·éæ©ç®æ èç¹");
      return;
    }
    this.taskSvc.excuteTask(this.inWorkitemId, this.completeContext).subscribe(
      (cr: CompleteResponse) => {
        this.commentModal.close();
        // å®æå·¥ä½é¡¹åï¼æ£æ¥æ¯å¦éè¦æ§è¡åäºä»¶
        this.excutePostEvent();
        if (this.completeContext.conclusionValue === 'Pass') {
          this.notify.success(this.i18nService.getValue('cueWords.successPass'));
        } else if (this.completeContext.conclusionValue === 'Back') {
          this.notify.success(this.i18nService.getValue('cueWords.successBack'));
        } else if (this.completeContext.conclusionValue === 'Refuse') {
          this.notify.success(this.i18nService.getValue('cueWords.successRefuse'));
        } else {
          this.notify.success(this.i18nService.getValue('cueWords.success'));
        }
        if (cr.needInteraction) {
          this.refreshActions();//éè¿åå¼¹åºææ´¾ add by fpf
          this.assigneeInfos = [cr.nextFlowNodes[0].assigneeInfo];
        } else {
          this.completedEvt();
        }
      }, error => {
        this.showError(error);
      }); 
  }
  // åæ¶æä½
  handleCancel() {
    this.completeContext = new CompleteContext();
    this.commentModal.close();
  }
  // ææ´¾æä½
  private assignee(workItemId: string) {
    this.taskSvc.getAssigneeInfo(workItemId).subscribe((info: FlowNodeInfo) => {
      info.assigneeInfo.workItemId = workItemId;//å¼å®¹å¤çï¼æ­¤å¤è¿åçworkitemidä¸ºnull
      this.assigneeInfos = [info.assigneeInfo];
    }, error => {
      this.showError(error);
    });
  }
  // éæ°åå§åå½ååæ°
  private initState() {
    this.selectAddSignees = '';
    this.destActivities = null;
    this.needAddSign = false;
  }

  private executeTask(actionCode: string, opinion?: string) {
    this.initState();
    this.completeContext = {
      workItemId: this.inWorkitemId,
      conclusionValue: actionCode,
      comment: '',
      signAssignees: [], // å ç­¾äººå
      destinationActivityId: ''// ç®æ èç¹
    };
    this.completeContext.comment = actionCode === 'Pass' ? this.actionsTitle.pass : '';
    if (opinion) {
      this.completeContext.comment = opinion;
    }
    const action = this.taskActions.find(a => a.code === actionCode);
    this.taskSvc.getActionEntity(this.workitemId, actionCode).subscribe((act: ActionEntity) => {

      if (!act) {// å®¡æ¹å¨ä½å·²å é¤ï¼èµ°å®æå·¥ä½é¡¹  TODO: éå®æå·¥ä½é¡¹å¨ä½å¦ä½å¤çï¼
        this.showExcuteModal(action);
      } else {
        from(this.excuteAction(act)).subscribe(() => {
          if (!act.webComponent) {
            this.showExcuteModal(action);
          }
        });
      }
    }, error => {
      this.showError(error);
    });
  }
  
  /**
   * å ç­¾ï¼è·³è½¬æåµ
   * @param action å¨ä½
   */
  private showExcuteModal(action: WorkitemAction) {
    // å¨ä½åæ°æç§
    if (action && action.parameters) {
      // è·³è½¬ãé©³ååºæ¯
      if (action.parameters.SelectTargetAble === 'True' || action.parameters.SelectTargetAble === 'true') {
        // this.taskSvc.getDestinationScope(this.inWorkitemId, action.code).subscribe((re: { id: string, name: string }[]) => {
        //   this.destActivities = re;
        // });
        this.getDestinationNodes(this.inWorkitemId, action.code);
      }
      // éè¦å ç­¾
      if (action.parameters.AddSignBehindAble === 'True' || action.parameters.AddSignBehindAble === 'true') {
        this.needAddSign = true;
      }
    }
    this.commentModal.show();
  }

  private getDestinationNodes(workItemId: string, actionCode: string) {
    var nextNodes: NextNode[] = [];
    this.taskSvc.getDestinationScope(workItemId, actionCode).subscribe((re: NextNode[]) => {
      nextNodes = re;
      const des = re.map(a => a.nodeInfoList);
      let list =[];
      des.forEach(i => {
        list = list.concat(i);
      });
      this.destActivities = list.map(i => ({id:i.activityDefinitionId, name:i.activityDefinitionName}));
      this.hasDefaultBranch = this.hasDefaultBranchFunc(nextNodes);
    }, error => {
      this.showError(error);
    });
  }

  /**
   * è·³è½¬ï¼è¿åçæ°æ®æ¯å¦æé»è®¤åæ¯
   * å¦æè¿åçæ²¡æé»è®¤èç¹ï¼è·³è½¬èç¹åæ²¡éçè¯ï¼ä¸åè®¸æäº¤
   * @param nextNodes 
   */
  private hasDefaultBranchFunc(nextNodes: NextNode[]) {
    for(let i = 0; i < nextNodes.length; i++) {
      if(nextNodes[i].defaultBranch) {
        this.displayPlaceholder = nextNodes[i].nodeInfoList[0].activityDefinitionName;
        return true;
      }
    }
    return false;
  }
  private async excuteAction(act: ActionEntity) {
    await this.getActionWebEventsFunc(act);
    if (this.preFuncList && this.preFuncList.length > 0) {
      for (let i = 0; i < this.preFuncList.length; i++) {
        this.preFuncList.filter(item => item.index === i)[0].func();
      }
    }
    if (this.webCptFunc) {
      this.webCptFunc();
    }
  }
  private excutePostEvent() {
    if (this.postFuncList && this.postFuncList.length > 0) {
      for (let i = 0; i < this.postFuncList.length; i++) {
        this.postFuncList.filter(item => item.index === i)[0].func();
      }
    }
  }
  /**
   * è·åå¨ä½çååäºä»¶åå®ç°æ¹æ³
   * @param act å¨ä½
   */
  private async getActionWebEventsFunc(act: ActionEntity) {
    this.preFuncList = [];
    this.webCptFunc = null;
    this.postFuncList = [];
    const reqMap = [];
    let preIndex = 0;
    let postIndex = 0;
    if (act.preEvent && act.preEvent.length > 0) {
      for (const e of act.preEvent) {
        const pre = {
          event: 'pre',
          index: preIndex++,
          funcName: e.component.split('#')[1],
          req: System.import(e.component.split('#')[0])
        };
        reqMap.push(pre);
      }
    }
    if (act.webComponent) {
      const cpt = {
        event: 'cpt',
        index: 0,
        funcName: act.webComponent.split('#')[1],
        req: System.import(act.webComponent.split('#')[0])
      };
      reqMap.push(cpt);
    }
    if (act.postEvent && act.postEvent.length > 0) {
      for (const a of act.postEvent) {
        const post = {
          event: 'post',
          index: postIndex++,
          funcName: a.component.split('#')[1],
          req: System.import(a.component.split('#')[0])
        };
        reqMap.push(post);
      }
    }
    for (const r of reqMap) {
      const m = await r.req;
      if (r.event === 'pre') {
        this.preFuncList.push({
          index: r.index,
          func: m.default[r.funcName]
        });
      } else if (r.event === 'post') {
        this.postFuncList.push({
          index: r.index,
          func: m.default[r.funcName]
        });
      } else {
        this.webCptFunc = m.default[r.funcName];
      }
    }
  }

  /**
   * æ¾ç¤ºæ¥éä¿¡æ¯
   */
  private showError(error: any) {
    if(error && error.error && error.error.Message) {
      this.notify.error(error.error.Message);
    } else {
      this.notify.error(error);
    }
  }
}
