import { Injectable, ComponentFactoryResolver, EventEmitter, Injector, Optional } from '@angular/core';
import { BsModalService, ModalOptions } from '@farris/ui-modal';
import { ProcessSelectorComponent } from '../process-selector/process-selector.component';
import { ExcutionResponse } from '../../entity/excution-response';
import { InteractionResponse } from '../../entity/interaction-response';
import { ProcessDefinition } from '../../entity/processdefinition';
import { AssigneeInfo } from '../../entity/complete-response';
import { AssignParticipantComponent } from '../assign-participant/assign-participant.component';
import { Subject, Observable, of } from 'rxjs';
import { I18nService } from '../../runtime/services/i18n.service';

/**
 * æäº¤å®¡æ¹å®¢æ·ç«¯äº¤äºæå¡
 */
@Injectable()
export class WFSubmiteService {

    constructor(
        private injector: Injector,
        private resolver: ComponentFactoryResolver,
        private modalService: BsModalService,
        @Optional() private i18nService: I18nService
    ) { }

    /**
     * å®¢æ·ç«¯äº¤äºå¤çæå¡ç«¯è¿åç»æ
     * @param excutionRe æäº¤å®¡æ¹æå¡å¨ç«¯è¿åç»æ
     * @param handler å¤çç»æåè°é¡µé¢
     */
    excute(excutionRe: ExcutionResponse, handler: (result: InteractionResponse) => void): void {
        const response = new InteractionResponse();
        // æ éäº¤äº
        if (excutionRe.needInteraction === false) {
            response.message = 'æ éäº¤äº';
            handler(response);
            return;
        }
        // éæ©è¦æäº¤çæµç¨
        if (excutionRe.procDefs && excutionRe.procDefs.length > 1) {
            this.selectProcess(excutionRe.procDefs, handler);
            return;
        }
        // æå®ä¸ä¸æ­¥åä¸è
        if (excutionRe.nextFlowNodes[0].assigneeInfo) {
            this.assignee([excutionRe.nextFlowNodes[0].assigneeInfo], handler);
            return;
        }
    }

    /**
     * å¤çéæ©æµç¨ç»æ
     */
    private selectProcess(prcesses: ProcessDefinition[], handler: (result: InteractionResponse) => void): void {
        const response = new InteractionResponse();
        const modalConfig: ModalOptions = {
            title: this.i18nService ? this.i18nService.getValue('static.submit.selectProcess') : 'æµç¨éæ©',
            width: 520,
            height: 290,
            showButtons: false,
            showMaxButton: false,
            beforeClose: (modelRef: any) => {
                handler(response);
                return of(true);
            }
        };
        const compFactory = this.resolver.resolveComponentFactory(ProcessSelectorComponent);
        const compRef = compFactory.create(this.injector);
        compRef.instance.gridData = prcesses;
        const dialog = this.modalService.show(compRef, modalConfig);
        compRef.instance.subscribe().subscribe(val => {
            response.processDefinitionId = val;
            dialog.close();
        }, err => {
            response.message = err;
            dialog.close();
        });
    }
    /**
     * å¤çæå®ä¸ä¸æ­¥äººåç»æ
     */
    private assignee(assigneeInfos: AssigneeInfo[], handler?: (result: InteractionResponse) => void) {
        const response = new InteractionResponse();
        const modalConfig: ModalOptions = {
            title: this.i18nService ? this.i18nService.getValue('static.assignee.title') : 'è¯·éæ©æä½ç¨æ·',
            width: 850,
            height: 520,
            showMaxButton: false,
            showButtons: false,
            beforeClose: (modelRef: any) => {
                handler(response);
                return of(true);
            }
        };
        const compFactory = this.resolver.resolveComponentFactory(AssignParticipantComponent);
        const compRef = compFactory.create(this.injector);
        compRef.instance.assigneeInfo = assigneeInfos;
        const dialog = this.modalService.show(compRef, modalConfig);
        compRef.instance.subscribe().subscribe(val => {
            dialog.close();
        }, err => {
            dialog.close();
        });
    }
}
