import { Component, OnInit, Input, HostBinding, Optional, ViewChild } from '@angular/core';
import { AssigneeInfo, AssigneeInfoByProcInstIdParameter } from '../../entity/complete-response';
import { UserService } from '../../runtime/services/user.service';
import { Observable, Subject } from 'rxjs';
import { NotifyService } from '@farris/ui-notify';
import { TaskService } from '../../runtime/services/task.service';
import { I18nService } from '../../runtime/services/i18n.service';
import { DataTableComponent } from '@farris/ui-datatable';
import { PaginationInstance } from '@farris/ui-pagination';
import { LoadingService } from '@farris/ui-loading'

@Component({
  selector: 'wf-assign-participant',
  templateUrl: './assign-participant.component.html',
  styleUrls: ['./assign-participant.component.css']
})
export class AssignParticipantComponent implements OnInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';

  private pageList = [10, 20, 30, 50, 100];
  paginationOptions: PaginationInstance = {
    id: 'WF-Users-DataTable-Pagination',
    currentPage: 1,
    itemsPerPage: 10,
    pageList: this.pageList,
    totalItems: 0
  };

  @Input()
  set assigneeInfo(assigneeInfos: AssigneeInfo[]) {
    this.assignees = assigneeInfos;
    this.assigneesPa = {
      procInstId: '',//åç«¯ç¨ä¸å° åä¸ºç©º
      assigneeInfos: []
    };
    this.getUserList();
  }

  private assignees: AssigneeInfo[];
  private assigneesPa: AssigneeInfoByProcInstIdParameter;
  private allowGlobalAssign: boolean;
  private subject = new Subject<AssigneeInfoByProcInstIdParameter>();
  private withAllOrg: boolean = true;
  private orgId: string = '';
  userListData = [];
  // æ¯å¦åè®¸å¤é,é»è®¤å¤é
  singleSelect = false;
  searchValue = null;
  selectedUsers: {
    ID: string,
    Code: string,
    Name: string,
    OrgId: string,
    OrgName: string
  }[] = [];
  @ViewChild('allUserTable') userTable: DataTableComponent;
  columns = [
    { title: this.i18nService ? this.i18nService.getValue('static.columnName.code') : 'ç¼å·', field: 'Code', width: 80 },
    { title: this.i18nService ? this.i18nService.getValue('static.columnName.name') : 'å§å', field: 'Name', width: 90 },
    { title: this.i18nService ? this.i18nService.getValue('static.columnName.department') : 'ç»ç»ä¿¡æ¯', field: 'OrgName', width: 180 }];
  constructor(
    protected userSvc: UserService,
    protected notify: NotifyService,
    private taskSvc: TaskService,
    private loadingService: LoadingService,
    @Optional() protected i18nService: I18nService
  ) {
  }

  ngOnInit() {
  }

  getUserList() {
    this.singleSelect = !this.assignees[0].multiSelect;
    if (this.assignees[0].assigneeUsers && this.assignees[0].assigneeUsers.length) {
      this.getAssignedUsers();
    } else {
      if (this.assignees[0].allowGlobalAssign) {
        if (this.assignees[0].organizationScope && this.assignees[0].organizationScope.length) {
          this.withAllOrg = this.assignees[0].organizationScope[0].includeChildren;
          this.orgId = this.assignees[0].organizationScope[0].id;
        }
        this.getAllUserList();
      } else {
        this.getAssignedUsers();
      }
    }
  }

  getAssignedUsers() {
    if (this.assignees.length > 0) {
      this.selectedUsers = this.assignees[0].assigneeUsers.map(user => {
        return {
          ID: user.id,
          Name: user.name,
          Code: user.code,
          OrgId: user.orgId,
          OrgName: user.orgNamePath
        };
      }),
        this.userListData = this.selectedUsers;
    }
  }

  protected getAllUserList() {
    const load = this.loadingService.show();
    this.userSvc.getUserList_Assign(this.searchValue, this.paginationOptions.currentPage, this.paginationOptions.itemsPerPage, this.withAllOrg, this.orgId)
      .subscribe((re: { totalCount: number, data: any[] }) => {
        load.close();
        this.paginationOptions.totalItems = re.totalCount;
        this.userListData = re.data.map(u => ({
          ID: u.id,
          Code: u.code,
          Name: u.name,
          OrgId: u.sysOrgId,
          OrgName: u.orgNamePath,
          Checked: false
        }));
      }, error => {
        load.close();
      });
  }

  search(event) {
    this.searchValue = event.value;
    if (this.assignees[0].assigneeUsers && this.assignees[0].assigneeUsers.length) {//æåéäººï¼å¨åéäººä¸­ç­é
      if(this.searchValue) {
        this.userListData = this.selectedUsers.filter(user =>
          (user.Name) && user.Name.includes(this.searchValue) ||
          (user.Code) && user.Code.includes(this.searchValue)
        );
      } else {
        this.getAssignedUsers();
      }
    } else {//æ åéäººï¼å¨å¨åä¸­éæ©
      this.paginationOptions.currentPage = 1;
      this.getUserList();
    }
  }

  btnCancelClick() {
    this.notify.info(this.i18nService ? this.i18nService.getValue('static.assignee.cancelAssign') : 'å·²åæ¶æä½ã');
    this.subject.next(null);
  }
  btnOKClick() {
    this.selectedUsers = this.singleSelect ? [this.userTable.selections] : this.userTable.selections;

    if (!this.selectedUsers || this.selectedUsers.length < 1) {
      this.notify.warning(this.i18nService ? this.i18nService.getValue('static.assignee.noAssignee') : 'è¯·è³å°éæ©ä¸ä¸ªå®¡æ¹äººã');
      return;
    }

    // æ·»å å·²éäººå
    const assigneeInfo = this.assignees.shift();
    assigneeInfo.assigneeIds = this.selectedUsers.map(user => user.ID);
    this.assigneesPa.assigneeInfos.push(assigneeInfo);

    // æ£æ¥æå®æ¯å¦å®æï¼å¦æå®ææäº¤ï¼å¦åç»§ç»­ææ´¾ä»»å¡
    if (this.assignees.length > 0) {
      this.userListData = []; // clear source
      this.getUserList();
    } else {
      const load = this.loadingService.show();
      this.taskSvc.assignNextUsersByProcInstId(this.assigneesPa).subscribe(() => {
        load.close();
        this.notify.success(this.i18nService ? this.i18nService.getValue('static.assignee.successAssign') : 'æä½æåã');
        this.subject.next(this.assigneesPa);
      }, err => {
        this.notify.error(this.i18nService ? this.i18nService.getValue('static.assignee.errorAssign') : 'æä½å¤±è´¥ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
        this.subject.error(this.i18nService ? this.i18nService.getValue('static.assignee.errorAssign') : 'æä½å¤±è´¥ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
      });
    }
  }
  subscribe(): Observable<AssigneeInfoByProcInstIdParameter> {
    return this.subject.asObservable();
  }

  onPageSizeChange(pageSize) {
    this.paginationOptions.itemsPerPage = pageSize;
    this.getUserList();
  }

  onPageChange(event) {
    this.paginationOptions.currentPage = event.pageIndex;
    this.getUserList();
  }

}
