import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UserHelperComponent } from '../user-helper/user-helper.component';
import { TaskService } from '../../services/task.service';
import { UserService } from '../../services/user.service';
import { NotifyService } from '@farris/ui-notify';
import { I18nService } from '../../services/i18n.service';

@Component({
  selector: 'wf-task-signadd',
  templateUrl: '../user-helper/user-helper.component.html',
  styleUrls: ['./task-signadd.component.css']
})
export class TaskSignaddComponent extends UserHelperComponent implements OnInit {

  @Input()
  public set addsign_context(context: any) {
    this.selectedUsers = [];
    this.addSignContext = { ...context, assignees: [] };
    this.showModal(!!this.addSignContext.workItemId);
  }
  @Output()
  completed = new EventEmitter<any>();

  // æ§è¡å ç­¾ä¸ä¸æåæ°
  addSignContext: {
    workItemId: string,
    action: 'AddSignFront' | 'AddSignBehind',
    assignees: string[],
    comment: ''// TODO: å ç­¾æ¥å¿
  };
  constructor(
    private taskSvc: TaskService,
    protected userSvc: UserService,
    protected notify: NotifyService,
    protected i18nService: I18nService
  ) {
    super(i18nService, userSvc);
    this.paginationOptions.id = 'WF-SignAdd-DataTable-Pagination';
  }

  ngOnInit() {
  }

  handleOk(): void {
    this.selectedUsers = this.userTable.selections;
    // å¾è¯¢
    if (this.addSignContext.action === 'AddSignFront') {
      // éè¦æ ¡éªéä¸­ç¨æ·
      if (!this.selectedUsers || this.selectedUsers.length < 1) {
        this.notify.info(this.i18nService.getValue('static.addsign.selectConsultant'));
        return;
      }
      this.addSignContext.assignees = this.selectedUsers.map(user => user.ID);
      this.taskSvc.addSign(this.addSignContext).subscribe(() => {
        this.notify.success(this.i18nService.getValue('static.addsign.successConsult'));
        this.completed.emit(true);
        this.showModal(false);
      }, error => {
        if(error && error.error && error.error.Message) {
          this.notify.error(error.error.Message);
        }
      });
    } else {
      // å ç­¾
      this.completed.emit(true);
      this.showModal(false);
    }
  }

}
